import {
	uniCollapse,
	uniCollapseItem,
} from '@dcloudio/uni-ui'
export default {
	data() {
		return {
			orderData: [ // 订单数据
				{    // 订单类型
					orderType: 1, // 0全部 1酒店、2火车票、3飞机票、4汽车票
					price: 58, // 价格
					orderState: 2 ,// 订单状态 1 待支付 2 已取消
					
					stationInfo: {
						startCity: '成都东',
						endCity: '隆昌北',
						startDate: '1-19', // 出发日期
						startTime: '07:30', // 出发时间
					},
					hotelInfo: {
						hotel: '成都明宇酒店',
						sDate: '1-19',//入住时间
						eDate: '1-20',// 离开时间
						lights: '共1晚',
					}
				},
				{    // 订单类型
					orderType: 2, // 1酒店、2火车票、3飞机票、4汽车票
					stationInfo: {
						startCity: '成都东',
						endCity: '隆昌北',
						startDate: '1-19', // 出发日期
						startTime: '07:30', // 出发时间
					},
					hotelInfo: {
						hotel: '成都明宇酒店',
						sDate: '1-19',//入住时间
						eDate: '1-20',// 离开时间
						lights: '共1晚',
					},
					price: 58, // 价格
					orderState: 2 // 订单状态 1 待支付 2 已取消
				},
				{    // 订单类型
					orderType: 3, // 1酒店、2火车票、3飞机票、4汽车票
					stationInfo: {
						startCity: '成都东',
						endCity: '隆昌北',
						startDate: '1-19', // 出发日期
						startTime: '07:30', // 出发时间
					},
					hotelInfo: {
						hotel: '成都明宇酒店',
						sDate: '1-19',//入住时间
						eDate: '1-20',// 离开时间
						lights: '共1晚',
					},
					price: 58, // 价格
					orderState: 1 // 订单状态 1 待支付 2 已取消
				},
				{    // 订单类型
					orderType: 4, // 1酒店、2火车票、3飞机票、4汽车票
					stationInfo: {
						startCity: '成都东',
						endCity: '隆昌北',
						startDate: '1-19', // 出发日期
						startTime: '07:30', // 出发时间
					},
					hotelInfo: {
						hotel: '成都明宇酒店',
						sDate: '1-19',//入住时间
						eDate: '1-20',// 离开时间
						lights: '共1晚',
					},
					price: 58, // 价格
					orderState: 1 // 订单状态 1 待支付 2 已取消
				}
			],
			topbarData: {
				leftHandle: false,
				title: "订单中心",
				left: "",
				right: "搜索图标"
			},
			isend: false, // 查看有效订单
			isall: false, // 查看全部
			istime: false, // 查看下单时间
			ismask: false, // 遮罩层
			tiemIndex: 0, // 选择下单时间的索引, 1 从近到远 2 早到晚
			Type: 0, // 0全部  1酒店、2火车票、3飞机票、4汽车票
			isReadingPay: true, // 立即支付防抖
		};
	},
	onLoad(){
	this.getOrderList()	
	},
	onShow() {
		this.isReadingPay = true
	},
	methods: {
		// 获取订单列表
		getOrderList(){
			let _this = this
			let Timestamp = new Date().toLocaleString()
			uni.request({
				url: this.$slurl + '/Flight/GetOrderList',
				method: 'POST',
				data: {
					
					"ApiKey": this.$ApiKey,
					    "Sign": "",
					    "Timestamp": Timestamp,
					    "Data": {
					        "PageSize": 40,
					        "PageIndex": 1,
					    }
				},
				success(res){
					console.log('全部订单列表返回数据', res)
				}
			})
		},
		// 取消订单 //先是机票
		quxiaoOrder(){
			let _this = this
			let Timestamp = new Date().toLocaleString()
			uni.request({
				url: this.$slurl + '/Flight/CancelOrder',
				method: 'POST',
				data: {
					"ApiKey": this.$ApiKey,
					    "Timestamp": Timestamp,
					    "Sign": "",
					    "Data": {
					        "OrderNo": "AF2004271736468304",
					        "Remark": "无无无无无无无无无无无无无无"
					    }
				},
				success(res) {
					console.log('取消订单返回数据', res)
					if(res.data.IsSuccess){
						uni.showToast({
							title: '取消成功'
						})
					}
				}
			})
		},
		childbar(val) {
			switch (val.identify) {
				case "leftHandle":
					break;
				case "rightHandle":
					this.$router.push({
						path: "/pwdreg",
						query: {
							tag: "reg",
							btn: "reg"
						}
					});
					break;
			}
		},
		open(){
		         this.$refs.popup.open()
		     },
		// 切换查看有效订单按钮状态
		changesee(){
			this.isend = !this.isend;
		},
		// 切换all
		changeall(){
			if(this.istime) {
				this.istime = false
				this.isall = !this.isall;
				return
			}
			this.isall = !this.isall;
			this.ismask = !this.ismask;
		},
		// 切换time
		changetime(){
			if(this.isall) {
				this.isall = false
				this.istime = !this.istime
				return
			}
			this.istime = !this.istime;
			this.ismask = !this.ismask;
		},
		// 选择下单时间类型切换
		changeTimeIndex(index){
			this.tiemIndex = index
		},
		// 选择分类切换
		changeAllIndex(target){
			if(target == 0){
				this.Type = 0
			}
			if(target == 1){
				this.Type = 1
			}
			if(target == 2){
				this.Type = 2
			}
			if(target == 3){
				this.Type = 3
			}
			if(target == 4){
				this.Type = 4
			}
		},
		// 点击遮罩层关闭当前选择
		cloce(){
			this.isall = false;
			this.istime = false;
			this.ismask = false;
		},
		// 立即支付
		payClick(){
			if(this.isReadingPay){
				this.isReadingPay = false
				uni.navigateTo({
					url: '../../ticket/toPaid/toPaid?type=' + 2
				})
			}
			
		}
	},
	components: {
		uniCollapse,
		uniCollapseItem,
	}
}
